.board-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    color: white;
}

.board-container .item {
    margin: 5px;
    text-align: center;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(25% - 11px);
    padding: 10px;
    align-items: center;
    justify-content: center;
    max-width: 70px;
}

.board-container .item::after{
    content: '';
    display: block;
    padding-bottom: 100%;
  }

.tile {
    position: relative;
    display: flex;
    background-image: linear-gradient(#F9D368, #F56F2F);
    border-radius: 8px;
    border: 4px solid transparent;
    background-clip: padding-box;
    box-sizing: border-box;
    margin: auto;
}

.selected {
    background-image: linear-gradient(#F36263, #A9273C);
}

.active {
    background-image: linear-gradient(#B3E85B, #46932A);
    border-radius: 8px;
    border: 2px solid;
}

.tile p {
    font-size: 1.8rem;
    margin: 0;
    font-weight: bold;
    text-shadow: -1px -1px 3px rgba(0, 0, 0, 0.35)
}

.tile::before {
    content: '';
    position: absolute;
    top: -2px; 
    right: -2px; 
    bottom: -2px;
    left: -2px;
    z-index: -1;
    border-radius: inherit;
    background: linear-gradient(#F36263, #A9273C);
}

.active::before {
    content: unset;
}


.reset-button {
    color: #D8D8D8;
    display: flex;
    align-items: center;
    align-self: flex-end;
    cursor: pointer;
    flex-basis: 0 !important;
}

.reset-button > svg {
    flex: 1 1 auto;
    margin-left: 15px;
}

.input-container {
    border: solid #A4A4A4 2px;
    padding: 13.5px !important;
    display: flex;
    max-width: 100% !important;
    margin-top: auto !important;
    flex-basis: 0 !important;
    justify-content: space-between;
}


p {
    margin: 0;
}

.word {
    font-size: 1.6rem;
    font-weight: bold;
    width: 80%;
    height: 31px;
    letter-spacing: 5px;
}

.word-valid {
    color: #81D135 !important;
}

.word-invalid {
    color: rgb(209, 16, 41);
}

.input-invalid {
    color: rgba(209, 16, 41, 0.4);
}

.input-valid {
    color: rgb(129, 209, 53, 0.4);
}
html, body {
    font-family: 'Verdana';
    margin: 0;
}

.global-container {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    margin: 0 15%;
    overflow: visible;
}

.inner-container {
    max-height: 352px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.item {
    padding: 20px;
    flex-grow: 0;
    max-width: 50%;
    flex-basis: 50%;
}

.sz-1 {
    max-width: 352px !important;
}

.sz-2 {
    max-width: 352px !important;
}

.sz-3 {
    padding-top: 30px;
}

.container-2 {
    display: flex;
    flex-direction: column;
}

@media only screen and (max-width: 800px) {
    .global-container {
        margin: 0 5%;
    }
}

@media only screen and (max-width: 768px) {
    .global-container, .inner-container {
        margin: 0 calc((100% - 351px) / 2);
        flex-direction: column;
        justify-content: space-around;
    }

    .item {
        padding: 20px 0;
        flex-grow: 0;
        flex-basis: unset;
    }

    .sz-1 {
        padding: calc(100vh - 662px) 0px;
    }

    .sz-2 {
        height: auto;
        width: 100%;
        max-width: 100% !important;
        flex-grow: 0;
    }

    .sz-3 {
        margin-left: auto;
    }

    .container-2 {
        height: auto;
        width: 100%;
    }
}
